// import { PricingContent } from "./booking_content/booking_pricing_content";
import { BookingDetailsContent } from "./booking_content/booking_details_content";
import { cn } from "../../utils/custom_functions";

export function BookingCard({ booking, ...props }) {
  return (
    <div className="rounded-md pb-6">
      <h2
        className={cn("text-start text-slate-800 font-extrabold text-lg", {
          "text-red-600": booking?.booking_canceled,
          "text-gray-800": booking?.booking_completed,
        })}
      >
        {booking?.id > 0 && (
          <div className="pt-2">
            <span>
              Invoice {booking?.id}{" "}
              {booking?.booking_canceled && <span>cancelled</span>}
              {booking?.booking_completed && <span>completed</span>}
            </span>
          </div>
        )}
      </h2>
      {/*Booking content card*/}
      <BookingDetailsContent booking={booking} />
      {/* <div className="py-1" /> */}
      {/*Booking content card*/}
      {/*Edit booking*/}
      {props.children}
      {/*Edit booking*/}
    </div>
  );
}
